<template>
  <div>
    <div class="cp_ipselect">
      <select @change="getSelectTxt" :id="tgt" class="cp_sl02" required>
        <option value="" hidden disabled selected></option>
        <option v-for="item in ChoicesItems" :key="item.id" :value="item">{{ item }}</option>
      </select>
      <span class="cp_sl02_highlight"></span>
      <span class="cp_sl02_selectbar"></span>
      <label class="cp_sl02_selectlabel">{{ choseTitle }}</label>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'select-box',
    props: {
      tgt: {
        type: String,
        requierd: true,
      },
      ChoicesItems: {
        type: Array,
        requierd: true,
      },
      choseTitle: {
        type: String,
        requierd: true,
      },
    },
    methods: {
      getSelectTxt: function () {
        const tgt = document.getElementById(this.tgt);
        let idx = tgt.selectedIndex;
        let txt = tgt.options[idx].text;
        this.$store.dispatch('assignTxt', txt);
      },
    },
  };
</script>

